import React, { useEffect, useState } from 'react'
import InfiniteScroll from 'react-infinite-scroll-component'
import Spinner from './Spinner'
import WallpaperCard from './WallpaperCard'

function Wallpaper(props) {
    const [hits, sethits] = useState([])
    const [loading, setloading] = useState(false)
    const [page, setpage] = useState(1)
    const [totalResults, settotalResults] = useState(0)

    const updateWallpaper = async () => {

        const url = `https://pixabay.com/api/?key=${props.apikey}`

        setloading(true)
        let data = await fetch(url)
        let newData = await data.json()

        console.log(newData.hits);
        sethits(newData.hits)
        settotalResults(newData.total)
        setloading(false)

    }
    useEffect(() => {

        updateWallpaper();
        // eslint-disable-next-line
    }, [])


    const fetchMoreData = async () => {




        const url = `https://pixabay.com/api/?key=${props.apikey}&page=${page + 1}`
        setpage(page + 1)
        let data = await fetch(url)
        let newData = await data.json()
        sethits(hits.concat(newData.hits))
        settotalResults(newData.total)

    }

    return (
        <div className="container">

            <h2 className={`text-center text-${props.textColor}`} style={{marginTop:"4rem"}}>New Wallpapers | WallpaperWorld</h2>

            {loading && <Spinner />}

            <InfiniteScroll
                dataLength={hits.length}
                next={fetchMoreData}
                hasMore={hits.length !== totalResults}
                loader={<Spinner />} >


                <div className="row container ">
                    {/* if loading is not true than show the spinner
              otherwise show the content. remember this syntex below */}

                    {hits.map((element) => {

                        return <div className="md-4 my-3 col" key={element.id}>
                            {/* dont forget to return after using map */}
                            <WallpaperCard imgUrl={element.webformatURL} tag={element.tags}
                                views={element.views} downloads={element.downloads} largeImageURL={element.largeImageURL} mode={props.mode}
                                textColor={props.textColor} likes={element.likes} user={element.user}
                            />

                        </div>

                    })}
                </div>

            </InfiniteScroll>


        </div>



    )
}

export default Wallpaper;
