import React from 'react'

function Navbar(props) {
    let { toggleMode, mode,title } = props
    return (
        <div>
            <nav className={`navbar fixed-top navbar-expand-lg navbar-${mode} bg-${mode}`}>
                <div className="container-fluid">
                    <a className="navbar-brand" href="/">{title}</a>
                    <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarNav" aria-controls="navbarNav" aria-expanded="false" aria-label="Toggle navigation">
                        <span className="navbar-toggler-icon"></span>
                    </button>
                    <div className="collapse navbar-collapse justify-content-end" id="navbarNav">
                        <ul className="navbar-nav">




                        </ul>
                        <div className="mx-4 form-check form-switch">
                            <input onClick={toggleMode} className="form-check-input" type="checkbox" id="flexSwitchCheckDefault" />
                            <label className={`form-check-label text-${mode === "dark" ? 'light' : 'dark'}`}
                                htmlFor="flexSwitchCheckDefault">  {mode === "light" ? "Enable Dark" : "Disable Dark"} Mode</label>

                        </div>

                    </div>
                </div>
            </nav>
        </div>
    )
}

export default Navbar
