import React from 'react'

function WallpaperCard(props) {

    let { tag, imgUrl, views, downloads,largeImageURL, mode, textColor, likes, user } = props;
   

    return (
        <div>
            <div className={`card my-3 text-${textColor} bg-${mode}`} style={{width: "18rem"}}>
                <img src={imgUrl? imgUrl: "https://thumbs.dreamstime.com/b/no-image-available-icon-flat-vector-no-image-available-icon-flat-vector-illustration-132482953.jpg"} className="card-img-top" alt="..." />
                <div className="card-body">
                    <h5 className="card-title">Tags: {tag}</h5>
                    <p className="card-text"> Total Views:{views} <br/> Total Likes:{likes} <br/> Total Downloads:{downloads} <br/> Captured By: {user}</p>
                    <a href={largeImageURL} target="_blank" rel="noreferrer" className={`btn btn-sm btn-outline-${textColor}`}>Open image</a>
                </div>
            </div>
        </div>
    )
}

export default WallpaperCard;
